                    "data": "{\"version\":2,\"template\":\"KnowledgeCards\",\"type\":\"knowledgeCards\",\"title\":\"Footfall Case Studies\",\"duration\":\"1:00\",\"introCopy\":\"Swipe through the cards to find out more\",\"theme\":\"No theme\",\"cards\":[{\"title\":\"Case Study 1 - Elle Magazine\",\"copy\":\"<br> In the US, Elle magazine used a combination of <strong>geofencing<\\/strong> and <strong>beacons<\\/strong> for its 'Shop Now!' campaign, making editors' product picks available to readers via ShopAdvisor, the personal shopping app, and RetailMe Not, the mobile coupon app. <br>Any opt-in consumer could receive a <strong>push notification<\\/strong> when they were near a partnering retailer such as Levi's and Barnes & Noble, before receiving a discount offer if they chose to visit a store. During 5 weeks, the campaign drove 500,000 store visits across 800 physical stores.\",\"image\":\"\"},{\"title\":\"Case Study 2 - KFC\",\"copy\":\"<br> KFC used <strong>mobile location targeting<\\/strong> to promote a new burrito range to relevant audiences near KFC stores, including young adults and busy parents with families. Partnering with location specialist xAd, the campaign used a combination of <strong>proximity targeting<\\/strong> (serving ads to consumers near a certain location), and <strong>conquest targeting<\\/strong> (serving ads at competitor locations), as well as <strong>dynamic creative content<\\/strong> showing the distance to the closest KFC store from a user's location. The campaign drove 18,000 store visits in 3 months.\",\"image\":\"\"}],\"lesson\":\"Footfall Case Studies\"}",
