                    "data":"{\"template\":{\"template\":\"pause-and-continue\",\"className\":\"PauseAndContinue\"},\"title\":\"Lesson Recap\",\"profile\":\"text\",\"interactions\":[{\"heading\":\"Here's what we've covered...\",\"text\":\"<strong>&bull; The Social Connection<\\/strong><br>We showed you how social and messaging apps lead the way in grabbing user attention on mobile, defining an evolving app landscape.<br><br><strong>&bull; Location Technologies<\\/strong><br>We introduced the main location technologies enabling mobile to bridge the worlds of online advertising and real-world sales.<br><br><strong>&bull; Mobile Payments<\\/strong><br>We guided you through the diverse world of mobile payments, including the various offerings reshaping in-store experiences.<br><br><strong>&bull; The Internet of Things<\\/strong><br>We explored mobile\’s place in a wider universe of connected devices and how it's helping marketers to better understand consumers.\"}],\"duration\":\"\"}",
