                    "duration": "0:00",
                    "displayduration": null,
                    "menu": "1",
                    "type": "test",
                    "ord": "1",
                    "clickable": "1",
                    "website": "1",
                    "data":"{\"version\": 2, \"type\": \"postAssessment\", \"template\": {\"template\": \"assessment\", \"className\": \"PostAssessment\"},\"title\": \"Post Assessment\",\"questions\": [{\"question\":\"Where do consumers spend the majority of their time on mobile?\",\"correct\":\"1\",\"choices\":\"[\\\"In apps\\\",\\\"Browsing the mobile web\\\",\\\"Time spent on mobile is approximately equal between apps and the mobile web\\\",\\\"There are no defining patterns between time spent in app and on the mobile web\\\"]\",\"type\":\"choice\",\"answer\":\"\"},{\"question\":\"Which category of app now dominates user attention on mobile?\",\"correct\":\"4\",\"choices\":\"[\\\"Audio\\\",\\\"Entertainment\\\",\\\"Gaming\\\",\\\"Social Media & Messaging\\\"]\",\"type\":\"choice\",\"answer\":\"\"},{\"question\":\"Which of the following best describes 'footfall'?\",\"correct\":\"2\",\"choices\":\"[\\\"The number of shoppers near a physical store in a given time\\\",\\\"The number of shoppers entering a physical store in a given time\\\",\\\"The number of shoppers making a purchase in a physical store in a given time\\\",\\\"The number of shoppers leaving a physical store without a purchase in a given time\\\"]\",\"type\":\"choice\",\"answer\":\"\"},{\"question\":\"Which of these technologies is generally most effective for measuring location indoors?\",\"correct\":\"3\",\"choices\":\"[ \\\"Cellular Network technology\\\",\\\"GPS\\\",\\\"Wi-Fi\\\",\\\"NFC Terminals\\\"]\",\"type\":\"choice\",\"answer\":\"\"},{\"question\":\"Which of these technologies would be most effective for determining which aisle a consumer is browsing in a supermarket?\",\"correct\":\"2\",\"choices\":\"[\\\"Geo-fencing\\\",\\\"Bluetooth beacons\\\",\\\"NFC terminals\\\",\\\"Roaming\\\"]\",\"type\":\"choice\",\"answer\":\"\"},{\"question\":\"What is often considered a limitation of beacon technology?\",\"correct\":\"1\",\"choices\":\"[\\\"Users need to have the correct app installed to listen out for the signals\\\",\\\"Users need to be connected to a Wi-Fi network\\\",\\\"Users need to have their location settings enabled\\\",\\\"Users need to be within 10 metres of the beacon\\\"]\",\"type\":\"choice\",\"answer\":\"\"},{\"question\":\"An advertiser could use beacon technology to\…?\",\"correct\":\"4\",\"choices\":\"[\\\"Deliver targeted messaging to phone numbers within 200m of a store\\\",\\\"Dynamically optimise ad creative based on local weather conditions\\\",\\\"Enable a consumer to make a purchase directly through a retail app\\\",\\\"Analyse how much attention a particular in-store display receives\\\"]\",\"type\":\"choice\",\"answer\":\"\"},{\"question\":\"The term 'mobile payment' refers to which of these?\",\"correct\":\"4\",\"choices\":\"[\\\"Using a branded app to pay in-store\\\",\\\"Purchasing a product via a mobile site\\\",\\\"Making a Peer-to-Peer money transfer\\\",\\\"All of the above\\\"]\",\"type\":\"choice\",\"answer\":\"\"},{\"question\":\"Branded payment apps, such as Starbucks, are examples of which type of mobile wallet?\",\"correct\":\"2\",\"choices\":\"[\\\"Open Loop\\\",\\\"Closed Loop\\\",\\\"Single Loop\\\",\\\"Double Loop\\\"]\",\"type\":\"choice\",\"answer\":\"\"},{\"question\":\"Mobile wallets such as Apple and Android Pay generally use which type of technology?\",\"correct\":\"1\",\"choices\":\"[\\\"NFC\\\",\\\"Bluetooth\\\",\\\"GPS\\\",\\\"Wi-Fi\\\"]\",\"type\":\"choice\",\"answer\":\"\"},{\"question\":\"'Closed loop' mobile wallets typically allow a user to\…?\",\"correct\":\"3\",\"choices\":\"[\\\"Make in-store purchases at a wide variety of retailer outlets\\\",\\\"Link a number of credit and debit cards to a single mobile wallet\\\",\\\"Make in-store purchases with a specific brand or retailer\\\",\\\"Make a payment through a social media app-feed\\\"]\",\"type\":\"choice\",\"answer\":\"\"},{\"question\":\"What has been a key advantage of \\\"Shop Now\\\" buttons on social media platforms?\",\"correct\":\"3\",\"choices\":\"[\\\"They enable cookies to function within the app environments\\\",\\\"Consumers are more likely to make purchases on social sites than on any other site\\\",\\\"They reduce the number of clicks along a path to purchase\\\",\\\"They reduce the need for ads to be optimised according to the individual\\\"]\",\"type\":\"choice\",\"answer\":\"\"},{\"question\":\"What is likely to improve how digital audio integrates with mobile for targeting strategies?\",\"correct\":\"4\",\"choices\":\"[\\\"The emergence of voice assistants\\\",\\\"Enhancements in digital sound quality\\\",\\\"The growth of the 'second-screen' phenomenon\\\",\\\"Wider availability of programmatic digital audio ad inventory\\\"]\",\"type\":\"choice\",\"answer\":\"\"},{\"question\":\"Which of the following best describes what is meant by a 'consumer-first' approach?\",\"correct\":\"3\",\"choices\":\"[\\\"Delivering the most impactful messaging on a consumer's preferred device\\\",\\\"Prioritising individual engagement over reach\\\",\\\"Delivering a seamless experience for a consumer across devices\\\",\\\"Allowing users full control over the ads they see\\\"]\",\"type\":\"choice\",\"answer\":\"\"},{\"question\":\"Personalising ads based on a user's location is most likely to increase\…?\",\"correct\":\"4\",\"choices\":\"[\\\"Reach\\\",\\\"Frequency\\\",\\\"Deliverability\\\",\\\"Relevance\\\"]\",\"type\":\"choice\",\"answer\":\"\"},{\"question\":\"Which of these is true of app usage for the majority of consumers?\",\"correct\":\"3\",\"choices\":\"[\\\"A wide variety of apps are heavily used\\\",\\\"Between 10 and 25 apps are heavily used\\\",\\\"10 or fewer apps are heavily used\\\",\\\"There are no definitive patterns of daily consumer app usage\\\"]\",\"type\":\"choice\",\"answer\":\"\"},{\"question\":\"Which of these statements best describes consumers' attitudes to mobile app usage?\",\"correct\":\"3\",\"choices\":\"[\\\"Apps are generally retained on a phone for at least one year\\\",\\\"Many apps are used simultaneously and on a daily basis\\\",\\\"More functionality is expected from existing apps\\\",\\\"Less than 80% of mobile time is spent within apps\\\"]\",\"type\":\"choice\",\"answer\":\"\"}]}",
                    "order": "1",
