            "name": "Attribution",
            "uri": "attribution",
            "description": "Attribution describes the technique of assigning credit or allocating spend from a sale to the marketing touchpoints that a customer was exposed to along their path to purchase. In digital marketing, the aim of attribution is to allow marketers to understand which events (e.g. seeing a TV or display ad, comparing prices on search comparison sites etc.) genuinely influence an individual to perform a conversion, which could be in the form of a product purchase or other conversion events such as competition sign ups or website registration. Once there is an understanding of which touchpoints are generating positive outcomes, attribution modelling techniques can use the cost of each touchpoint to evaluate which ones are most profitable, enabling better optimisation of ad spend. Early attribution models include ‘First Click' and ‘Last Click’ attribution, while more advanced methods are geared around what is known as ‘multi-touch attribution’.",
