            "uri": "cookie-1",
            "description": "In its most basic form, a cookie is a small text file (usually containing a site name and a unique user ID) that a website delivers into a user's temp files through their browser. If the user returns to the website, the browser sends the information in the cookie back to the site. This enables the site to recognise a user who has visited before. The web communications usage of cookies was pioneered by Lou Montulli, the founding engineer for Netscape Communications, in 1994. Cookies come in several forms, the most notorious of which is the 'third party tracking cookie'. Most cookies are perfectly benign and make web browsing smoother, but recent EU legislation has made it illegal for most sites to cookie users without first telling them how they intend to use their data.",
